<template>
  <div id="myFace" v-show="show" @click="ShutDown">
    <div class="face_area">
      <img v-for="(item, index) in face_box" @click="faceInsert(item)" :src="item" :key=index>
    </div>
  </div>
</template>

<script>
import { ROOM_FACE_BOX } from '../common/utils/face'

export default {
  name: 'myFace',
  props: {
    showClick: {
      type: Boolean,
      default: false
    }
  },
  data () {
    return {
      face_box: ROOM_FACE_BOX,
      show: false
    }
  },
  watch: {
    showClick: {
      handler (newVal, oldVal) {
        if (this.showClick) {
          this.show = true
        } else {
          this.show = false
          this.$emit('shutDown')
        }
      },
      immediate: true
    }
  },
  methods: {
    ShutDown () {
      this.show = false
      this.$emit('shutDown')
    },
    faceInsert (item) {
      this.show = false
      this.$emit('faceInsert', item)
    }
  }
}
</script>

<style scoped>
  #myFace {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    bottom: 0;
    background-color: rgba(0, 0, 0 0);
  }

  #myFace .face_area {
    position: fixed;
    width: 100%;
    height: 30%;
    left: 0;
    bottom: 4rem;
    background-color: white;
    overflow-y: auto;
  }

  #myFace .face_area img {
    width: 2.1rem;
    height: 2rem;
    padding: 0.5rem;
  }
</style>



