<template>
  <div id="disElse">
    <div class="disElse_left" @touchstart="touch">
      <div class="portrait">
        <img :src="imgSrc">
      </div>
    </div>
    <div class="disElse_right">
      <p>{{text_name | phoneHidden}}</p>
      <div class="disElse_text"><a v-html="text"></a></div>
      <div class="dis_icon"></div>
    </div>
  </div>
</template>

<script>
import { faceInsert, htmlEscape, toMine } from '../common/utils/face'

let touchTime
export default {
  name: 'disElse',
  props: {
    text_data: String,
    text_name: String,
    ToContent: {
      type: Array,
      default: () => []
    },
    imgSrc: {
      type: String,
      default: require('../common/images/客服.jpg')
    }
  },
  data () {
    return {
      text: ''
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    // 进行转换去掉防止用户输入html数据
    init () {
      this.text = htmlEscape(this.text_data)
      this.text = faceInsert(this.text)
      this.text = toMine(this.text, this.ToContent)
    },
    touch () {
      touchTime = setTimeout(() => {
        this.touch_confirm()
      }, 1000)
    },
    touch_confirm () {
      clearTimeout(touchTime)
      this.$emit('touchConfirm', this.text_name)
    }
  },
  destroyed () {
    clearTimeout(touchTime)
  }
}
</script>

<style scoped>
  #disElse {
    position: relative;
    margin: 1rem 0;
  }

  #disElse .dis_icon {
    background-color: #F5F5F5;
    position: absolute;
    width: 1rem;
    height: 1rem;
    left: -0.3rem;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
    top: 2rem;
    z-index: -1;
  }

  #disElse .disElse_left {
    width: 3rem;
    padding: 0 0 0 0.5rem;
    display: inline-block;
    position: absolute;
  }

  #disElse .disElse_left .portrait {
    border-radius: 2.5rem;
    width: 2.5rem;
    height: 2.5rem;
    background-color: aliceblue;
  }

  #disElse .disElse_left .portrait img {
    width: 2.5rem;
    border-radius: 1.25rem;
  }

  #disElse .disElse_right {
    display: inline-block;
    position: relative;
    left: 4rem;
  }

  #disElse .disElse_right p {
    margin: 0 auto;
    font-size: 0.9rem;
    color: #666666;
  }

  #disElse .disElse_right .disElse_text {
    width: 16rem;
    background-color: #F5F5F5;
    border-radius: 4px;
    padding: 0.6666rem;
    color: #333333;
    word-break: break-all;
  }

  #disElse .disElse_right .disElse_text a {

  }

  #disElse .disElse_right .disElse_text a img {

  }
</style>



