<template>
  <div id="disShow">
    <div class="dis_time" v-if="timeShow">{{time}}</div>
    <disMine v-if="text_value.self" :text_data="text_value.text" :ToContent="text_value.ToContent"
             :text_name="text_value.name"></disMine>
    <disElse v-else :text_data="text_value.text" :ToContent="text_value.ToContent" @touchConfirm="touchConfirm"
             :text_name="text_value.name"></disElse>
  </div>
</template>

<script>
import disElse from './disElse'
import disMine from './disMine'

export default {
  name: 'disShow',
  props: {
    text_value: Object
  },
  components: {
    disMine,
    disElse
  },
  data () {
    return {
      time: '',
      timeShow: false
    }
  },
  mounted () {
    this.getTime()
  },
  activated () {
    // this.getTime()
  },
  created () {
  },
  methods: {
    getTime () {
      console.log('获取时间')
      let now = new Date()
      let nowTime = new Date(this.text_value.time)
      let nowH = nowTime.getHours()
      let nowM = nowTime.getMinutes()
      if (nowH < 10) {
        nowH = '0' + nowH
      }
      if (nowM < 10) {
        nowM = '0' + nowM
      }
      let oldTime = this.$store.state.MessageTime
      let d = now.getDate()
      let m = now.getMonth() - nowTime.getMonth()
      if (d === 1 && now.getMonth() - nowTime.getMonth() === 1) {
        d += nowTime.getDate()
        m = 0
      }
      // console.log(nowTime.getTime(), oldTime)
      if (nowTime.getTime() - oldTime < 300000) {
        this.timeShow = false
      } else if (d - nowTime.getDate() === 0 && m === 0) {
        this.time = nowH + ':' + nowM
        this.timeShow = true
        this.$store.commit('setMessageTime', nowTime.getTime())
      } else if (d - nowTime.getDate() === 1 && m === 0) {
        this.time = '昨天' + nowH + ':' + nowM
        this.timeShow = true
        this.$store.commit('setMessageTime', nowTime.getTime())
      } else {
        this.time = this.text_value.time
        this.timeShow = true
        this.$store.commit('setMessageTime', nowTime.getTime())
      }
      // debugger
    },
    touchConfirm (name) {
      this.$emit('touchConfirm', name)
    }
  }
}
</script>

<style scoped>
  #disShow {

  }

  #disShow .dis_time {
    text-align: center;
    width: 100%;
  }
</style>



