<template>
  <div id="myChat" @keydown.enter="sent">
    <div v-show="andShow&&!moreShow" class="chat_control">
      <div class="chat_switch" @click="chatSwitch">{{chat_switch}}
        <div class="red_clock" ref="red_clock"></div>
      </div>
      <input v-model="input_data" ref="chat_input" style="left: 3rem;width: 13rem" class="chat_input"
             placeholder="我来说两句…">
      <div class="chat_expression" @click="showface"><img src="../common/images/添加表情.png"></div>
      <div class="chat_sent" @click="sent" @keydown.enter="sent">发送</div>
    </div>
    <div v-if="!andShow&&!moreShow" class="chat_control">
      <input v-model="input_data" class="chat_input" ref="chat_input" placeholder="我来说两句…">
      <div class="chat_expression" @click="showface"><img src="../common/images/添加表情.png"></div>
      <div class="chat_sent" @click="sent" @keydown.enter="sent">发送</div>
    </div>
    <div v-if="!andShow&&moreShow" class="chat_control" ref="chat_control">
      <input v-model="input_data" class="chat_input" style="width: 15rem;left: 1rem;background-color: #F5F5F5;"
             ref="chat_input" placeholder="输入你的问题吧">
      <div class="chat_expression" ref="chat_expression" @click="showmore"><img src="../common/images/moreNew.png">
      </div>
      <div class="chat_sent" @click="sent" @keydown.enter="sent">发送</div>
      <div class="chat_more" v-show="more_Show">
        <div class="more_img" @click="showimg">
          <input style="display: none" ref="imgInput" @change="addImg" type="file" accept="image/*" id="upload"
                 name="upload"><img
          src="../common/images/添加图片.png"></div>
        <div class="more_expression" @click="showface"><img src="../common/images/添加表情.png"></div>
        <div class="more_sentEva" v-if="false" @click="sentEva"><img src="../common/images/发送评价.png"></div>
      </div>
    </div>
    <div v-if="andShow&&moreShow" class="chat_control" ref="chat_control">
      <div class="chat_switch" @click="chatSwitch"><img :src="imgSrc">
        <div class="red_clock" ref="red_clock" style="display: none"></div>
      </div>
      <!--      <div v-html="input_data" @input="inputD" @focusin="focusChange" class="chat_input" contenteditable="true" ref="chat_input" placeholder="我来说两句…"></div>-->
      <input v-model="input_data" @focusin="focusChange" class="chat_input" ref="chat_input" placeholder="我来说两句…">
      <div class="chat_expression" ref="chat_expression" @click="showmore"><img src="../common/images/moreNew.png">
      </div>
      <div class="chat_sent" @click="sent" @keydown.enter="sent">发送</div>
      <div class="chat_more" v-show="more_Show">
        <div class="more_img" @click="showimg">
          <input style="display: none" ref="imgInput" @change="addImg" type="file" accept="image/*" id="upload"
                 name="upload">
          <img src="../common/images/添加图片.png"></div>
        <div class="more_expression" @click="showface"><img src="../common/images/添加表情.png"></div>
        <div class="more_sentEva" v-if="false" @click="sentEva"><img src="../common/images/发送评价.png"></div>
      </div>
    </div>
    <my-face :show-click="showFace" @shutDown="shutDown" @faceInsert="faceInsert"></my-face>
  </div>
</template>

<script>
import myFace from './myFace'
import { ROOM_FACE_BOX } from '../common/utils/face'
import myUtil from '../common/myUtil'

export default {
  name: 'myChat',
  components: {
    myFace
  },
  props: {
    andShow: {
      type: Boolean,
      default: false
    },
    moreShow: {
      type: Boolean,
      default: false
    },
    chat_input: {
      type: String,
      default: ''
    }
  },
  watch: {
    input_data: {
      handler (newVal, oldVal) {
        let toContent = this.$store.state.ToContent === null ? [] : this.$store.state.ToContent
        // console.log(toContent)
        let l
        if (oldVal === undefined) {
          l = 0
        } else {
          l = oldVal.length
        }
        if (newVal.length < l) {
          for (let i in toContent) {
            let word = '@' + toContent[i].name + ' '
            let index = newVal.indexOf(word)
            if (index < 0) {
              this.input_data = newVal.substring(0, toContent[i].start) + newVal.substring(toContent[i].end)
              // console.log(toContent[i].start, toContent[i].end)
              this.$delete(toContent, i)
              return
            }
          }
        } else if (newVal.length > l) {
          let chatInput = this.$refs.chat_input
          let start = chatInput.selectionStart
          for (let i in toContent) {
            let word = '@' + toContent[i].name + ' '
            let index = newVal.indexOf(word)
            if (index < 0) {
              this.input_data = newVal.substring(0, start - (newVal.length - l)) + newVal.substring(start)
              return
            }
          }
          console.log(start)
        } else {
        }
        this.$emit('inputChange', newVal)
      },
      immediate: true
    },
    chat_input: {
      handler (newVal, oldVal) {
        this.input_data = newVal
      },
      immediate: true
    }
  },
  data () {
    return {
      chat_switch: '@',
      showFace: false,
      face_data: ROOM_FACE_BOX,
      input_data: '',
      more_Show: false,
      imgSrc: require('../common/images/@.png')
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      let redClock = this.$refs.red_clock
      redClock.style.display = 'block'
    },
    showimg () {
      let imgInput = this.$refs.imgInput
      imgInput.click()
    },
    sentEva () {

    },
    focusChange () {
      let chatInput = this.$refs.chat_input
      let toContent = this.$store.state.ToContent === null ? [] : this.$store.state.ToContent
      let start = chatInput.selectionStart
      for (let i of toContent) {
        if (start > i.start && start < i.end) {
          chatInput.setSelectionRange(chatInput.value.length, chatInput.value.length)
        }
      }
    },
    addImg () {
      let imgInput = this.$refs.imgInput
      this.showmore()
      let reader = new FileReader()
      reader.readAsDataURL(imgInput.files[0])
      let _this = this
      reader.onload = function (e) {
        _this.input_data = '<img style="max-width: 12rem; padding: 1rem;" src="' + this.result + '" />'
        _this.sent()
      }
    },
    // getObjectURL (file) {
    //   let url = null
    //   if (window.createObjectURL !== undefined) { // basic
    //     url = window.createObjectURL(file)
    //   } else if (window.URL !== undefined) { // mozilla(firefox)
    //     url = window.URL.createObjectURL(file)
    //   } else if (window.webkitURL !== undefined) { // webkit or chrome
    //     url = window.webkitURL.createObjectURL(file)
    //   }
    //   return url
    // },
    showface () {
      if (this.showFace) {
        this.showFace = false
      } else {
        this.showFace = true
      }
      if (this.moreShow) {
        this.showmore()
      }
    },
    showmore () {
      localStorage.setItem('SAVE_CURSOR_INDEX', true)
      localStorage.setItem('CURSOR_SELECT_START', this.$refs.chat_input.selectionStart)
      localStorage.setItem('CURSOR_SELECT_END', this.$refs.chat_input.selectionEnd)
      let chatExpression = this.$refs.chat_expression
      let chatControl = this.$refs.chat_control
      if (this.more_Show) {
        this.more_Show = false
        chatControl.style.height = '3rem'
        chatExpression.style.webkitTransform = 'rotate(-90deg)'
      } else {
        this.more_Show = true
        chatControl.style.height = '10rem'
        chatExpression.style.webkitTransform = 'rotate(90deg)'
      }
    },
    faceInsert (val) {
      this.showFace = false
      let ele
      if (this.andShow && !this.moreShow) {
        ele = this.$refs.chat_input
      } else if (!this.moreShow) {
        ele = this.$refs.chat_input
      } else {
        ele = this.$refs.chat_input
      }
      for (let key in this.face_data) {
        if (this.face_data[key] === val) {
          myUtil.insertTo(ele, key)
          this.$emit('inputChange', ele.value)
        }
      }
      ele.focus()
    },
    shutDown () {
      this.showFace = false
    },
    sent () {
      if (this.input_data === '') {
        return
      }
      this.$emit('sent', this.input_data)
    },
    chatSwitch () {
      let redClock = this.$refs.red_clock
      if (this.chat_switch === '@') {
        this.chat_switch = '全'
        redClock.style.display = 'none'
        this.imgSrc = require('../common/images/全.png')
      } else {
        this.chat_switch = '@'
        redClock.style.display = 'block'
        this.imgSrc = require('../common/images/@.png')
      }
      this.$emit('chatSwitch', this.chat_switch)
    }
  }
}
</script>

<style scoped>
  #myChat {
  }

  #myChat .chat_control {
    width: 100%;
    height: 3rem;
    background-color: white;
    padding: 0.5rem;
    position: fixed;
    bottom: 0;
    transition: transform 0.5s linear;
  }

  #myChat .chat_control .chat_switch {
    display: inline-block;
    width: 2rem;
    height: 2rem;
    text-align: center;
    line-height: 2rem;
    font-weight: 700;
    position: absolute;
    top: 1rem;
    left: 1rem;
  }

  #myChat .chat_control .chat_switch img {
    width: 1.8rem;
    height: 1.8rem;
    line-height: 2rem;
  }

  #myChat .chat_control .chat_switch .red_clock {
    background-color: red;
    width: 0.5rem;
    height: 0.5rem;
    position: absolute;
    top: 0;
    right: 0;
    border-radius: 0.5rem;
  }

  #myChat .chat_control .chat_input {
    width: 12rem;
    background-color: aliceblue;
    height: 2rem;
    line-height: 2rem;
    position: absolute;
    left: 4rem;
    top: 0.8rem;
    border: 0;
    outline: none;
    padding-left: 1rem;
  }

  #myChat .chat_control .chat_expression {
    display: inline-block;
    /* margin: 1rem 0.5rem 0 0.5rem; */
    width: 2rem;
    text-align: center;
    height: 2rem;
    line-height: 2rem;
    position: absolute;
    /*bottom: 1.5rem;*/
    /* left: 1rem; */
    right: 7rem;
    transform-origin: 95% 70%;
    transition: transform 0.5s linear;
  }

  #myChat .chat_control .chat_expression img {
    width: 1.8rem;
    height: 1.8rem;
    line-height: 2rem;
    position: absolute;
    top: 0.5rem;
  }

  #myChat .chat_control .chat_more {

  }

  #myChat .chat_control .chat_more .more_img {
    position: absolute;
    top: 4rem;
    left: 2rem;
  }

  #myChat .chat_control .chat_more .more_expression {
    position: absolute;
    top: 4rem;
    left: 7rem;
  }

  #myChat .chat_control .chat_more .more_sentEva {
    position: absolute;
    top: 4rem;
    left: 12rem;
  }

  #myChat .chat_control .chat_sent {
    display: inline-block;
    margin: 0.4rem 0.5rem;
    width: 2rem;
    text-align: center;
    height: 2rem;
    line-height: 2rem;
    position: absolute;
    right: 2rem;
    color: #666666;
  }
</style>



