<template>
  <div id="DiscussionGroups">
    <my-dialog :show="dialog_show" @cancel="dialog_show=false" @confirm="confirm" :text="dialog_text"></my-dialog>
    <div class="chat_content" ref="chat_content" @touchmove="touchMove" @touchstart="touchStart" @touchend="touchEnd">
      <div class="content_loading" v-show="loadingShow" ref="loading">
        <div v-show="loadingShow" class="loading one"></div>
        <div v-show="loadingShow" class="loading two"></div>
        <div v-show="loadingShow" class="loading three"></div>
        <div v-show="loadingShow" class="loading four"></div>
        <p v-show="loadingShow">正在加载...</p>
      </div>
      <dis-show ref="disShow" :text_value="item" :key=index
                @touchConfirm="touchConfirm"
                v-for="(item,index) in chat_record"></dis-show>
    </div>
    <my-chat :and-show="true" :more-show="true" @sent="sent" @chatSwitch="chatSwitch" @inputChange="inputChange"
             :chat_input="chat_input"></my-chat>
  </div>
</template>

<script>
import disShow from '../../../../components/disShow'
import myDialog from '../../../../components/myDialog'
import myChat from '../../../../components/myChat'

import { protobufToPost } from '../../../../common/js/websocket'
// import myUtil from '../../../../common/myUtil'

export default {
  name: 'DiscussionGroups',
  components: {
    disShow,
    myDialog,
    myChat
  },
  data () {
    return {
      chat_switch: '@',
      dialog_show: false,
      dialog_text: '你已经发送成功',
      chat_input: '',
      my_name: '188****0510',
      location: -1, // 记录触摸点击时的初始位置
      loadingShow: false, // 正在加载是否渲染
      queryOnce: true, // 在下拉查询中记录状态，保证在一次满足条件的下拉中只查询一次
      chat_record: [
        {
          self: true,
          text: '你好',
          name: '188****0510',
          time: '2019-11-17 03:05:05',
          ToContent: []
        },
        {
          self: false,
          text: '你好，请稍等',
          name: '服务人员',
          time: '2019-11-17 03:06:6',
          ToContent: []
        },
        {
          self: false,
          text: '好的，请问你有什么问题呢',
          name: '服务人员',
          time: '2019-11-17 03:07:6',
          ToContent: []
        },
        {
          self: false,
          text: '没有问题的我将会在一分钟后离线[调皮]',
          name: '服务人员',
          time: '2019-11-17 03:09:6',
          ToContent: []
        },
        {
          self: false,
          text: '再见',
          name: '服务人员',
          time: '2019-11-17 03:10:6',
          ToContent: [{
            name: '188****0510'
          }]
        }
      ]
    }
  },
  computed: {
    ToContent: function () {
      return this.$store.state.ToContent
    }
  },
  mounted () {
    this.init()
    this.query()
  },
  methods: {
    init () {
      // let a = myUtil.deepClone(this.chat_record)
      sessionStorage.setItem('chatRecord', [])
      let ele = document.getElementById('DiscussionGroups')
      this.$Dialog.Prompt({
        ele: ele,
        topShow: true,
        textShow: true
      })
      let chatContent = this.$refs.chat_content
      chatContent.style.top = '5rem'
      // this.isAbleTalk()
    },
    // 查询是否被禁言
    isAbleTalk () {
      this.$http.isAbleTalk({
        userId: '11221'
      }).then((res) => {
        if (res.data) {
        }
      })
    },
    // 用户是否被拉黑
    isAbleEnter () {
      this.$http.isAbleEnter({
        userId: '11221'
      }).then((res) => {
        if (res.data) {
        }
      })
    },
    // 获取用户离线消息数
    getOfflineMsgCount () {
      this.$http.getOfflineMsgCount({
        userId: '11221'
      }).then((res) => {
        if (res.data) {
        }
      })
    },
    // 获取用户离线消息
    getOfflineMsg () {
      this.$http.getOfflineMsg({
        userId: '11221'
      }).then((res) => {
        if (res.data) {
        }
      })
    },
    // 查询消息
    listMsg () {
      this.$http.listMsg({
        userId: '11221',
        sortId: 1, // 0之前消息,1之后消息 默认1
        lastMsgSendTime: 1211, // 查询的起点时间节点
        msgNum: 121, // 消息数
        queryType: 0 // 0全部,1只看客服,2与我相关 默认0
      }).then((res) => {
        if (res.data) {
        }
      })
    },
    // 上传图片
    upload () {
      this.$http.upload({
        file: ''
      }).then((res) => {
        if (res.data) {
        }
      })
    },
    touchStart (event) {
      this.location = event.changedTouches[0].screenY
    },
    touchMove (event) {
      if (this.location > 0) {
        let loading = this.$refs.loading
        let y = event.changedTouches[0].screenY - this.location
        if (y > 0) {
          // 设置下拉到一定高度时才展示正在加载 之后才获取新数据，防止太灵敏的去获取聊天记录
          if (y > 60) {
            this.loadingShow = true
            if (this.queryOnce) {
              this.queryPass()
            }
          }
          y = y > 120 ? 120 : y
          loading.style.height = y / 2 + 'px'
          loading.style.paddingTop = y / 2 + 'px'
        }
      }
    },
    queryPass () {
      console.log('查询之前的消息')
      this.queryOnce = false
      setTimeout(() => {
        let message = [
          {
            self: false,
            text: '没有问题的我将会在一分钟后离线[调皮]',
            name: '服务人员',
            time: '2019-11-17 03:09:6',
            ToContent: []
          },
          {
            self: false,
            text: '再见',
            name: '服务人员',
            time: '2019-11-17 03:10:6',
            ToContent: [{
              name: '188****0510'
            }]
          }
        ]
        this.chat_record = message.concat(this.chat_record)
        this.touchEnd()
      }, 1000)
    },
    touchEnd () {
      this.location = -1
      let loading = this.$refs.loading
      loading.style.height = 0
      loading.style.paddingTop = 0
      this.loadingShow = false
      this.queryOnce = true
    },
    query () {
      localStorage.setItem('chatRecord', this.chat_record)
      this.getAnnouncement()
    },
    getAnnouncement () {
      this.$http.getAnnouncement({}).then((res) => {
      })
    },
    inputChange (val) {
      this.chat_input = val
      let toContent = this.ToContent === null ? [] : this.ToContent
      for (let i of toContent) {
        let word = '@' + i.name + ''
        let index = val.indexOf(word)
        if (index >= 0) {
          i.start = index
          i.end = index + word.length
        }
      }
    },
    chatSwitch (val) {
      if (val === '全') {
        let ele = document.getElementById('DiscussionGroups')
        this.$Dialog.Prompt({
          ele: ele,
          textShow: false,
          topShow: true
        })
        let recordData = []
        for (let i of this.chat_record) {
          for (let j of i.ToContent) {
            if (j) {
              recordData.push(i)
            }
          }
        }
        this.chat_record = recordData
      } else {
        this.chat_record = sessionStorage.getItem('chatRecord')
      }
    },
    touchConfirm (name) {
      let start = this.chat_input.length
      let end = start + name.length + 2
      this.chat_input += '@' + name + ' '
      // 以下代码存入@信息，以及输入框是实现@信息一下全部删除作用
      let toContent = this.ToContent === null ? [] : this.ToContent
      toContent.push({
        name: name,
        id: '111',
        start: start,
        end: end
      })
      this.$store.commit('setToContent', toContent)
    },
    confirm () {
      this.dialog_show = false
    },
    sent (val) {
      // val = faceInsert(val)
      let toContent = this.ToContent === null ? [] : this.ToContent
      for (let i of toContent) {
        val = val.substring(0, i.start) + val.substring(i.end + 1)
      }
      let message = {
        name: this.my_name,
        self: true,
        text: val,
        ToContent: toContent
      }
      this.chat_record.push(message)
      this.$nextTick(() => {
        this.sentMessage(val)
      })
      this.$store.commit('setToContent', [])
    },
    sentMessage (val) {
      let chatContent = this.$refs.chat_content
      chatContent.scrollTop = chatContent.scrollHeight
      this.chat_input = ''
      this.postWS(val)
    },
    postWS (val) {
      protobufToPost({
        'module': '14000', // 讨论模块
        'cmd': '101',
        'type': 1,
        'msg': {
          'teamId': 7,
          'msg': [{
            'type': 1,
            'textMsg': {
              'text': val
            }
          }]
        }
      }, (postData) => {
        window.ws.send(postData) // 发送消息
      })
    }
  }
}
</script>

<style scoped>
  #DiscussionGroups {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #FFFFFF;
    z-index: 0;
  }

  #DiscussionGroups .chat_content {
    width: 100%;
    overflow-y: auto;
    position: absolute;
    bottom: 4rem;
  }

  #DiscussionGroups .chat_content .content_loading {
    width: 9rem;
    height: 0;
    max-height: 25rem;
  }

  #DiscussionGroups div::-webkit-scrollbar {
    width: 2px;
    height: 0;
    display: none;
  }

  /*#DiscussionGroups .chat_control {*/
  /*  width: 100%;*/
  /*  height: 3rem;*/
  /*  background-color: white;*/
  /*  padding: 0.5rem;*/
  /*  position: fixed;*/
  /*  bottom: 0;*/
  /*}*/

  /*#DiscussionGroups .chat_control .chat_switch {*/
  /*  display: inline-block;*/
  /*  width: 2rem;*/
  /*  height: 2rem;*/
  /*  border-radius: 1rem;*/
  /*  background-color: darkcyan;*/
  /*  text-align: center;*/
  /*  line-height: 2rem;*/
  /*  font-weight: 700;*/
  /*  position: relative;*/
  /*}*/

  /*#DiscussionGroups .chat_control .chat_switch .red_clock {*/
  /*  background-color: red;*/
  /*  width: 0.5rem;*/
  /*  height: 0.5rem;*/
  /*  position: absolute;*/
  /*  top: 0;*/
  /*  right: 0;*/
  /*  border-radius: 0.5rem;*/
  /*}*/

  /*#DiscussionGroups .chat_control .chat_input {*/
  /*  width: 15rem;*/
  /*  background-color: aliceblue;*/
  /*  height: 2rem;*/
  /*  line-height: 2rem;*/
  /*}*/

  /*#DiscussionGroups .chat_control .chat_expression {*/
  /*  display: inline-block;*/
  /*  margin: 0.5rem;*/
  /*  width: 2rem;*/
  /*  text-align: center;*/
  /*  height: 2rem;*/
  /*  line-height: 2rem;*/
  /*}*/

  /*#DiscussionGroups .chat_control .chat_sent {*/
  /*  display: inline-block;*/
  /*  margin: 0.5rem;*/
  /*  width: 2rem;*/
  /*  text-align: center;*/
  /*  height: 2rem;*/
  /*  line-height: 2rem;*/
  /*}*/

  #DiscussionGroups .content_loading {
    padding: 1rem 8rem;
    z-index: -1;
    height: 0;
  }

  #DiscussionGroups .content_loading p {
    margin: 0 auto;
    text-align: center;
  }

  #DiscussionGroups .content_loading .loading {
    width: 1rem;
    height: 1rem;
    margin: 0.5rem;
    display: inline-block;
    border-radius: 0.5rem;
    background-color: #ff0000;
  }

  #DiscussionGroups .content_loading .one {
    animation: loading 0.5s ease 0.1s infinite alternate;
  }

  #DiscussionGroups .content_loading .two {
    animation: loading 0.5s ease 0.2s infinite alternate;
  }

  #DiscussionGroups .content_loading .three {
    animation: loading 0.5s ease 0.3s infinite alternate;
  }

  #DiscussionGroups .content_loading .four {
    animation: loading 0.5s ease 0.4s infinite alternate;
  }

  @keyframes loading {
    from {
      transform: scale(1)
    }

    to {
      transform: scale(0.6);
    }

  }
</style>



