<template>
  <div id="disMine">
    <div class="disMine_left">
      <p>{{text_name | phoneHidden}}</p>
      <div class="disMine_text"><a v-html="text"></a></div>
      <div class="dis_icon"></div>
    </div>
    <div class="disMine_right">
      <div class="portrait"><img :src="imgSrc"></div>
    </div>
  </div>
</template>

<script>
import { faceInsert, htmlEscape, toMine } from '../common/utils/face'

export default {
  name: 'disMine',
  props: {
    text_data: String,
    text_name: String,
    ToContent: {
      type: Array,
      default: () => []
    },
    imgSrc: {
      type: String,
      default: require('../common/images/客服.jpg')
    }
  },
  data () {
    return {
      text: ''
    }
  },

  mounted () {
    this.init()
  },
  methods: {
    init () {
      this.text = htmlEscape(this.text_data)
      this.text = faceInsert(this.text)
      this.text = toMine(this.text, this.ToContent)
    }
  }
}
</script>

<style scoped>
  #disMine {
    position: relative;
    text-align: right;
    margin: 1rem 0;
  }

  #disMine .dis_icon {
    background-color: #ECF5FF;
    position: absolute;
    width: 1rem;
    height: 1rem;
    right: -0.3rem;
    transform: rotate(45deg);
    top: 2rem;
    z-index: -1;
  }

  #disMine .disMine_right {
    width: 3rem;
    padding: 0 0 0 0.5rem;
    display: inline-block;
    position: absolute;
    right: 0;
  }

  #disMine .disMine_right .portrait {
    border-radius: 2.5rem;
    width: 2.5rem;
    height: 2.5rem;
    background-color: aliceblue;
  }

  #disMine .disMine_right .portrait img {
    width: 2.5rem;
    border-radius: 1.25rem;
  }

  #disMine .disMine_left {
    display: inline-block;
    position: relative;
    right: 4rem;
  }

  #disMine .disMine_left p {
    margin: 0 auto;
    color: #666666;
    font-size: 0.9rem;
  }

  #disMine .disMine_left .disMine_text {
    width: 16rem;
    background-color: #ECF5FF;
    border-radius: 4px;
    padding: 0.6666rem;
    color: #333333;
    word-break: break-all;
    text-align: left;
  }

  #disMine .disMine_left .disMine_text a {

  }

  #disMine .disMine_left .disMine_text a img {
    width: 2rem;
    height: 2rem;
  }
</style>



